.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.cute-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #ffe6f2;
}

.loader-heart {
  width: 80px;
  height: 80px;
  background: #ff69b4;
  position: relative;
  transform: rotate(-45deg);
  margin-bottom: 20px;
  animation: heartbeat 1s infinite;
}
.loader-heart:before,
.loader-heart:after {
  content: '';
  position: absolute;
  width: 80px;
  height: 80px;
  background: #ff69b4;
  border-radius: 50%;
}
.loader-heart:before {
  top: -40px;
  left: 0;
}
.loader-heart:after {
  left: 40px;
  top: 0;
}
@keyframes heartbeat {
  0%, 100% { transform: scale(1) rotate(-45deg); }
  20% { transform: scale(1.1) rotate(-45deg); }
  40% { transform: scale(0.95) rotate(-45deg); }
  60% { transform: scale(1.05) rotate(-45deg); }
  80% { transform: scale(0.97) rotate(-45deg); }
}
.loading-text {
  font-size: 1.5rem;
  color: #d63384;
  font-weight: bold;
}

.question-container {
  min-height: 100vh;
  background: #fff0f6;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.button-row {
  margin-top: 30px;
  display: flex;
  gap: 30px;
}
.yes-btn, .no-btn {
  font-size: 1.2rem;
  padding: 12px 32px;
  border-radius: 30px;
  border: none;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.2s, color 0.2s, transform 0.1s;
}
.yes-btn {
  background: #ffb3c6;
  color: #fff;
  box-shadow: 0 2px 8px #ffb3c6aa;
}
.yes-btn:hover {
  background: #ff69b4;
  color: #fff;
  transform: scale(1.08);
}
.no-btn {
  background: #fff;
  color: #ff69b4;
  border: 2px solid #ff69b4;
}
.no-btn:hover {
  background: #ff69b4;
  color: #fff;
  transform: scale(1.08);
}

.gallery-container {
  min-height: 100vh;
  background: #e0c3fc;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-top: 32px;
}
.gallery-placeholder {
  font-size: 1.3rem;
  color: #7c3aed;
  margin-top: 30px;
  background: #fff;
  padding: 20px 40px;
  border-radius: 20px;
  box-shadow: 0 2px 8px #7c3aed33;
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
  justify-items: center;
  margin-top: 10px;
}
.gallery-item {
  /* Remove background, padding, and box-shadow */
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s, box-shadow 0.2s;
  max-width: 220px;
  cursor: pointer;
  opacity: 1;
  filter: none;
}
.gallery-item:hover {
  transform: scale(1.06) rotate(-2deg);
  box-shadow: 0 4px 24px #ff69b4aa;
}
.gallery-item img {
  max-width: 200px;
  width: 100%;
  height: auto;
  max-height: 300px;
  border-radius: 16px;
  object-fit: cover;
  box-shadow: none;
  opacity: 1;
  filter: none;
}

.gallery-img {
  max-width: 200px;
  width: 100%;
  height: auto;
  max-height: 300px;
  border-radius: 16px;
  object-fit: cover;
  box-shadow: none;
  opacity: 1;
  filter: none;
  background: none;
  transition: transform 0.2s, box-shadow 0.2s;
  cursor: pointer;
}

.gallery-img:hover {
  transform: scale(1.06) rotate(-2deg);
  box-shadow: 0 4px 24px #ff69b4aa;
}

@media (max-width: 900px) {
  .gallery-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 600px) {
  .gallery-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  .gallery-item {
    max-width: 90vw;
  }
  .gallery-item img {
    max-width: 85vw;
    max-height: 30vh;
  }
  .gallery-img {
    max-width: 85vw;
    max-height: 30vh;
  }
}
.gallery-caption {
  margin-top: 30px;
  font-size: 1.4rem;
  color: #d63384;
  font-weight: bold;
  background: #fff0f6;
  padding: 12px 32px;
  border-radius: 20px;
  box-shadow: 0 2px 8px #ffb3c6aa;
}

.carousel-wrapper {
  width: 100%;
  max-width: 350px;
  margin: 0 auto 30px auto;
  background: #fff;
  border-radius: 24px;
  box-shadow: 0 2px 16px #7c3aed33;
  padding: 16px 0 32px 0;
}
.slick-slider {
  position: relative;
}
.slick-dots li button:before {
  color: #ff69b4;
  font-size: 16px;
  opacity: 0.7;
}
.slick-dots li.slick-active button:before {
  color: #d63384;
  opacity: 1;
}
.gallery-item {
  display: flex !important;
  align-items: center;
  justify-content: center;
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.5s;
  opacity: 0.7;
  filter: blur(1px);
}
.slick-active .gallery-item {
  opacity: 1 !important;
  filter: none !important;
  transform: scale(1.04);
  z-index: 2;
}
.gallery-item img {
  max-width: 300px;
  width: 100%;
  height: auto;
  max-height: 400px;
  border-radius: 18px;
  object-fit: cover;
  box-shadow: 0 2px 12px #ffb3c6aa;
  transition: box-shadow 0.3s, transform 0.3s;
}
.gallery-item img:hover {
  box-shadow: 0 4px 24px #ff69b4aa;
  transform: scale(1.07) rotate(-2deg);
}
@media (max-width: 700px) {
  .carousel-wrapper {
    max-width: 95vw;
    padding: 8px 0 16px 0;
  }
  .gallery-item img {
    max-width: 90vw;
    max-height: 50vh;
  }
}

.cake-party-section {
  margin-top: 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}
.cake-btn {
  background: #fff0f6;
  border: none;
  border-radius: 50%;
  padding: 18px 24px;
  font-size: 2.2rem;
  box-shadow: 0 2px 8px #ffb3c6aa;
  cursor: pointer;
  transition: background 0.2s, transform 0.2s;
  outline: none;
}
.cake-btn:active {
  background: #ffb3c6;
  transform: scale(1.08);
}
.cake-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.cake-emoji {
  font-size: 2.5rem;
}
.balloon-container {
  display: flex;
  gap: 18px;
  margin-top: 12px;
  font-size: 2.2rem;
  animation: balloons-float 2s ease-in-out;
}
@keyframes balloons-float {
  0% { transform: translateY(40px) scale(0.7); opacity: 0; }
  60% { opacity: 1; }
  100% { transform: translateY(-30px) scale(1.1); opacity: 1; }
}
.big-cake {
  font-size: 3.5rem;
  margin-top: 10px;
  animation: cake-pop 1.2s cubic-bezier(0.4, 0, 0.2, 1);
}
@keyframes cake-pop {
  0% { transform: scale(0.5) rotate(-10deg); opacity: 0; }
  60% { opacity: 1; }
  100% { transform: scale(1.1) rotate(5deg); opacity: 1; }
}

.party-overlay {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  z-index: 9999;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255,255,255,0.01);
}
.balloon-svg {
  position: absolute;
  width: 60px;
  height: 90px;
  background: none;
  z-index: 2;
  opacity: 0.9;
  animation: balloon-float 2.2s cubic-bezier(0.4,0,0.2,1) forwards;
}
.balloon1 {
  left: 20vw;
  bottom: 0;
  background: url('data:image/svg+xml;utf8,<svg width="60" height="90" xmlns="http://www.w3.org/2000/svg"><ellipse cx="30" cy="50" rx="25" ry="35" fill="%23ffb3c6"/><rect x="27" y="80" width="6" height="10" rx="3" fill="%23ff69b4"/></svg>') no-repeat center/contain;
}
.balloon2 {
  left: 50vw;
  bottom: 0;
  background: url('data:image/svg+xml;utf8,<svg width="60" height="90" xmlns="http://www.w3.org/2000/svg"><ellipse cx="30" cy="50" rx="25" ry="35" fill="%23ffe066"/><rect x="27" y="80" width="6" height="10" rx="3" fill="%23ffd700"/></svg>') no-repeat center/contain;
}
.balloon3 {
  left: 75vw;
  bottom: 0;
  background: url('data:image/svg+xml;utf8,<svg width="60" height="90" xmlns="http://www.w3.org/2000/svg"><ellipse cx="30" cy="50" rx="25" ry="35" fill="%2399e2b4"/><rect x="27" y="80" width="6" height="10" rx="3" fill="%2300b894"/></svg>') no-repeat center/contain;
}
@keyframes balloon-float {
  0% { transform: translateY(0) scale(0.7); opacity: 0.7; }
  60% { opacity: 1; }
  100% { transform: translateY(-60vh) scale(1.1); opacity: 0.9; }
}
.sparkle {
  position: absolute;
  width: 32px;
  height: 32px;
  background: url('data:image/svg+xml;utf8,<svg width="32" height="32" xmlns="http://www.w3.org/2000/svg"><circle cx="16" cy="16" r="8" fill="%23fff0f6" opacity="0.7"/><circle cx="16" cy="16" r="4" fill="%23ff69b4" opacity="0.8"/></svg>') no-repeat center/contain;
  z-index: 3;
  opacity: 0.8;
  animation: sparkle-pop 1.5s cubic-bezier(0.4,0,0.2,1) forwards;
}
.sparkle1 { left: 30vw; top: 30vh; }
.sparkle2 { left: 60vw; top: 40vh; }
.sparkle3 { left: 45vw; top: 20vh; }
@keyframes sparkle-pop {
  0% { transform: scale(0.5); opacity: 0; }
  60% { opacity: 1; }
  100% { transform: scale(1.2); opacity: 0; }
}
.big-cake-svg {
  position: absolute;
  left: 50%;
  top: 55%;
  transform: translate(-50%, -50%) scale(1.1);
  z-index: 10;
  animation: cake-pop 1.2s cubic-bezier(0.4, 0, 0.2, 1);
}
@keyframes cake-pop {
  0% { transform: translate(-50%, -50%) scale(0.5) rotate(-10deg); opacity: 0; }
  60% { opacity: 1; }
  100% { transform: translate(-50%, -50%) scale(1.1) rotate(5deg); opacity: 1; }
}
